.footer-container {
  background-color: #242424;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: white;
}

.footer-subscription > p {
  margin-bottom: 24px;
  font-size: 24px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.footer-container-heading {
  margin-bottom: 24px;
  font-size: 24px;
}

.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 90%;
  max-width: 1000px;
  margin: 10px auto 0 auto;
}

.footer-links-wrapper {
  display: flex;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items h2 {
  margin-bottom: 16px;
}

.footer-link-items > h2 {
  color: white;
}

.footer-link-items a {
  color: white;
  text-decoration: none;
  margin-bottom: 8px;
}

.footer-link-items a:hover {
  color: rgb(93, 255, 174);
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icon-link {
  font-size: 24px;
  color: white;
  padding: 5px;
}

.social-icon-link:hover {
  color: rgb(93, 255, 174);
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: rgb(37, 189, 130);
  text-decoration: none;
}

.website-rights {
  color: rgb(168, 168, 168);
}
